header {
    background-color: #004994;
    width: 100%;
    height: auto;
    position: fixed;
    display: block;
    top: 0;
    z-index: 1;
    opacity: 0.9;
  }
  
  h1 {
    text-align: center;
    margin: 0 auto 0 auto;
    font-family: "Caveat", cursive;
    font-size: 5.625rem;
    color: #3f93e8;
  }
  
  h2 {
    text-align: center;
    margin: 0;
    font-family: "Montserrat", sans-serif;
    font-size: 3rem;
    color: #3f93e8;
    font-weight: 900;
  }
  
  h3 {
    color: #004994;
    font-family: "Montserrat", sans-serif;
    margin-top: 0;
    margin-bottom: 1.3em;
  }
  
  h4 {
    display: inline-block;
    font-family: "Montserrat", sans-serif;
    font-size: 1em;
    color: #fafafa;
    margin-left: 20px;
    margin-right: 20px
  }
  
  h5 {
    font-family: "Montserrat", sans-serif;
    font-size: 2rem;
    color: #fafafa;
    margin-top: auto;
    margin-bottom: auto;
  }
  
  p {
    margin: auto;
    font-family: "Montserrat", sans-serif;
    color: #40514e;
  }
  
  hr {
    width: 4%;
    border-style: dotted none none none;
    color: #fafafa;
    border-spacing: 2px;
    border-width: 6px;
    margin: 30px auto;
  }
  
  a {
    color: #3f93e8;
  }
  
  body {
    text-align: center;
    margin: 0;
    line-height: 2;
    font-weight: 900;
  }
  
  /* HEADER */
  
  .action-bar {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: stretch;
    width: 85%;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    line-height: 1;
  
  }
  
  @media only screen and (max-width: 731px) {
    .action-bar {
      justify-content: center;
    }
  }
  
  .header-title {
    display: flex;
  }
  
  .header-nav {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: stretch;
  }
  
  .header-nav a {
    text-decoration: none;
  }
  
  .header-nav a:hover {
    color: #fafafa;
  }
  
  /* LANDING PAGE */
  
  .top-container {
    background-color: #cfecff;
    padding-top: 120px;
    position: relative;
  }
  
  .top-container h2 {
    font-style: italic;
  }

  .sun {
      position: absolute;
      right: 400px;
      top:60px;
      color: #fce9a9;
      animation: infinite-spinning 20s linear infinite
  }

  @keyframes infinite-spinning {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .bottom-cloud {
    position: absolute;
    right: -200px;
    bottom: 300px;
  }
  
  .top-cloud {
    position: absolute;
    left: -200px;
    top: 70px;
  }

  .lowest-cloud {
    position: absolute;
    left: -200px;
    bottom: 200px;
  }
  
  .cloud {
    color: #fafafa;
  }
  
  .mountain {
    line-height: 0;
    margin-right:auto;
    margin-right:auto;
  }
  
  /* PROFILE */
  
  .profile-container {
    background-color: white;
    padding: 100px 0;
  }
  
  .profile {
    width: 30%;
    margin: 0 auto 0 auto;
    font-weight: 900;
  }
  
  /* PORTFOLIO */
  
  .portfolio-container {
    background-color: #004994;
    padding: 100px 0;
  }
  
  .portfolio {
    width: 75%;
    text-align: center;
    margin: auto;
  }
  
  .portfolio h2 {
    color: white;
  }
  
  .portfolio h3 {
    color: white;
  }
  
  /* SKILLS */
  
  .skill-row {
    width: 50%;
    margin: 80px auto 80px auto;
    text-align: left;
    line-height: 2;
  }
  
  .skills {
    padding: 100px 0;
    background-color: #cfecff;
  }
  
  .skills hr {
    color: #3f93e8;
  }
  
  .pen-img {
    width: 24%;
    float: left;
    padding: 20px 30px 20px 0;
  }
  
  .computer-img {
    width: 26%;
    float: right;
    padding-left: 30px;
  }
  
  /* PROFESSIONAL RESOURCES */
  
  .employment-resources {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: stretch;
    margin: 60px auto 0 auto;
    width: 80%;
  }
  
  .employment-resources h3 {
    width: 50%;
    margin: auto;
  }
  
  .employment-resources a {
    display: flex;
    margin: 10px auto;
    width: 350px;
    height: 150px;
    text-align: left;
    text-decoration: none;
    border-radius: 10px;
  }
  
  .employment-resources a:hover {
    background-color: #fafafa;
    border: solid;
    border-color: #f0f0f0;
    border-width: 1px;
  }
  
  .resume {
    display: flex;
    margin: auto;
    width: 100%;
    height: auto;
    text-align: left;
  }
  
  .resume-pic {
    width: 25%;
    margin: auto;
  }
  
  .linkedin {
    display: flex;
    margin: auto;
    width: 100%;
    height: auto;
    text-align: left;
  }
  
  .linkedin-pic {
    width: 25%;
    margin: auto;
  }
  
  .twitter {
    display: flex;
    margin: auto;
    width: 100%;
    height: auto;
    text-align: left;
  }
  
  .twitter-pic {
    width: 25%;
    margin: auto;
  }
  
  /* CONTACT */
  
  .contact-me {
    padding: 100px;
  }
  
  .contact {
    display: flex;
    width: 90%;
    margin: auto;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  
  }
  
  .contact-container {
    width: 70%;
    border-style: solid;
    border-radius: 5px;
    border-color: #bfbfbf;
    border-width: .5px;
    margin:auto;
    padding: 40px 40px 80px 40px;
  
  }
  
  .contact-me hr {
    color: #3f93e8;
  }
  
  .explain-contact {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 500px;
    height: auto;
    margin: 40px auto;
  }
  
  .explain-contact img {
    display: flex;
    height: 100px;
    padding: 0 10px 10px 10px;
    margin: auto;
  }
  
  .email-cta {
    text-align: left;
    display: flex;
    width: 65%;
    margin: auto;
  }
  
  .email-form {
    text-align: center;
    display: flex;
    height: 30px;
    width: 50%;
    padding: 30px;
  }
  
  .email-input {
    display: block;
    line-height: 1.5rem;
    margin: 10px;
    width: 50%;
    font-family: "Montserrat", sans-serif;
    border-color: #bfbfbf;
    border-radius: 5px;
    border-width: 0.5px;
    border-style: solid;
  }
  
  .email-input:focus {
    border-color: #004994;
    outline: none;
  }
  
  .email-button {
    line-height: 1.5rem;
    margin: 10px;
    background-color: #cfecff;
    color: #004994;
    font-family: "Montserrat", sans-serif;
    font-weight: 900;
    font-size: 1.5rem;
    border-radius: 5px;
    border-color: #f0f0f0;
    border-width: 1px;
    border-style: solid;
    text-decoration: none;
    margin: auto;
    padding: 20px;
    display: block;
  }
  
  .email-button:hover {
    background-color: #004994;
    color: #fafafa;
  }
  
  /* FOOTER */
  
  .bottom-container {
    display: flex;
    flex-direction: column;
    position: relative;
    bottom: 0;
    padding: 15px;
    background-color: #cfecff;
  }
  
  .footer-link {
    text-decoration: none;
    font-size: 1rem;
    font-family: "Montserrat", sans-serif;
    margin-right: 10px;
  }
  
  .footer-link:hover {
    color: white;
  }
  
  .footer-copyright {
    font-size: 10px;
    margin: 0 auto;
    position: relative;
  }
  
  .footer-copyright p {
    color: #3f93e8;
  }
  
  .image-credit {
    font-size: 10px;
    width: 25%;
    text-align: center;
    margin: 20px auto 20px auto;
    text-decoration: none;
  }
